import { mapState, mapMutations, mapActions } from 'vuex'
import LoadingMore from '../../../components/loadingMoreTemplate'
import Parse from '../../../components/jyf-parser/jyf-parser'
import { upyunUrl } from '../../../api/config'

export default {
  data () {
    return {
      upyunUrl,
      pape: 1,
      date: {}
    }
  },
  onLoad () {
    this.page = 1
    this.newDateFn()
    this.getGrowthRecorFn(true)
  },
  computed: {
    ...mapState([
      'growhtRecordList'
    ])
  },
  methods: {
    ...mapMutations({
      setGrowhtRecordList: 'SET_GROWHT_RECORD_LIST'
    }),
    ...mapActions([
      'getGrowthRecord'
    ]),
    // 获取当前时间
    newDateFn () {
      const now = new Date()
      const year = now.getFullYear()
      const month = now.getMonth()
      const date = now.getDate()
      const day = now.getDay()
      this.date['year'] = year
      this.date['month'] = month + 1
      this.date['monthEnglish'] = this.monthEnglishFn(month + 1)
      this.date['date'] = date
      this.date['weekChinese'] = this.weekChineseFn(day)
      this.date['weekEnglish'] = this.weekEnglishFn(day)
    },
    // 月份转英文
    monthEnglishFn (month) {
      switch (month) {
        case 1:
          return 'January'
        case 2:
          return 'February'
        case 3:
          return 'March'
        case 4:
          return 'April'
        case 5:
          return 'May'
        case 6:
          return 'June'
        case 7:
          return 'July'
        case 8:
          return 'August'
        case 9:
          return 'September'
        case 10:
          return 'October'
        case 11:
          return 'November'
        case 12:
          return 'December'
        default:
          return false
      }
    },
    // 星期转中文
    weekChineseFn (week) {
      switch (week) {
        case 0:
          return '星期日'
        case 1:
          return '星期一'
        case 2:
          return '星期二'
        case 3:
          return '星期三'
        case 4:
          return '星期四'
        case 5:
          return '星期五'
        case 6:
          return '星期六'
        default:
          return false
      }
    },
    // 星期转英文
    weekEnglishFn (week) {
      switch (week) {
        case 0:
          return 'Sunday'
        case 1:
          return 'Monday'
        case 2:
          return 'Tuesday'
        case 3:
          return 'Wednesday'
        case 4:
          return 'Thursday'
        case 5:
          return 'Friday'
        case 6:
          return 'Saturday'
        default:
          return false
      }
    },
    // 获取成长记录
    getGrowthRecorFn (isLoading) {
      const data = {
        that: this,
        page: this.page
      }
      if (isLoading) {
        uni.showLoading({ title: 'Loading' })
        this.setGrowhtRecordList([])
      }
      this.getGrowthRecord(data).then(() => {
        if (isLoading) uni.hideLoading()
      })
    },
    // 显示-隐藏content
    onShowHideContentFn (index, status) {
      this.growhtRecordList[index]['isLine'] = status
    },
    // 预览图片
    onPreviewImageFn (arr, url) {
      uni.previewImage({
        current: url,
        urls: arr
      })
    },
    // 上拉加载更多
    onScrolltolowerFn () {
      this.page += 1
      this.getGrowthRecorFn(false)
    }
  },
  components: {
    Parse,
    LoadingMore
  }
}
